import { useEffect, useRef, useState } from 'react'
import { useLocation } from 'react-router-dom'
import { Offcanvas } from 'react-bootstrap'
import AlertMsg from '../globals/AlertMsg'

export default function Navigation({ navigate }){
    const { pathname } = useLocation()
    const [offCanvasNav, setOffCanvasNav] = useState('')
    const [alertModal, setAlertModal] = useState(false)

    const showOffCanvasNav = () => setOffCanvasNav(true)
    const hideOffCanvasNav = () => setOffCanvasNav(false)

    const hideAlertModal = () => setAlertModal(false)
    const onDonate = () => setAlertModal({
        showModal: true,
        msg: `
            Support Our Cause_____
            Account Name: Ultra Realhope_____
            Account number: 0026401231_____
            Bank: Access Bank
        `,
        size: 'xl',
        hideModal: hideAlertModal
    })

    const navigateTo = (path) => {
        hideOffCanvasNav()
        navigate(path)
    }

    const navLinksDisplay = 
    <>
        <div id='main-home-nav' className="main-nav-links mb-2 main-nav-link mx-2" onClick={() => navigateTo('')}>Home</div>
        <div id='main-about-nav' className="main-nav-links mb-2 main-nav-link mx-2" onClick={() => navigateTo('about')}>About Us</div>
        <div id='main-campaign-nav' className="main-nav-links mb-2 main-nav-link mx-2" onClick={() => navigateTo('campaigns')}>Campaigns</div>
        {/* <div id='main-contact-nav' className="main-nav-links mb-2 main-nav-link mx-2" onClick={() => navigateTo('contact')}>Contact</div> */}
        <div id='main-contact-nav' className="main-nav-links mb-2 main-nav-link mx-2">
            <a href='https://medium.com/@Realhope' target="_blank">Blog</a>
        </div>
        {/* <button id='main-donate-nav' className="main-nav-links main-nav-btn mx-2 py-2 px-4" onClick={() => navigateTo('donate')}>DONATE</button> */}
        <button id='main-donate-nav' className="main-nav-links main-nav-btn mx-2 py-2 px-4" 
            onClick={onDonate}>
            DONATE
        </button>
    </>
    

    useEffect(() => {
        const mainNavs = document.getElementsByClassName('main-nav-links')
        if(mainNavs.length > 0){
            for(let i = 0; i < mainNavs.length; i++){
                const { id } = mainNavs[i]
                if(id == 'main-donate-nav'){
                    mainNavs[i].classList.remove('active-main-nav-btn-link')
                } else{
                    mainNavs[i].classList.remove('active-main-nav-link')
                }
            }
            if(pathname == '/'){
                document.getElementById('main-home-nav').classList.add('active-main-nav-link')
            } else if(pathname == '/about'){
                document.getElementById('main-about-nav').classList.add('active-main-nav-link')
            } else if(pathname == '/campaigns'){
                document.getElementById('main-campaign-nav').classList.add('active-main-nav-link')
            } else if(pathname == '/contact'){
                document.getElementById('main-contact-nav').classList.add('active-main-nav-link')
            } else{
                document.getElementById('main-donate-nav').classList.add('active-main-nav-btn-link')
            }
        }
    }, [pathname])

    return(
        <article className="p-3 p-lg-1 fixed-top main-nav">
            <section className="d-lg-flex d-none justify-content-around align-items-center py-1">
                <section className="col-lg-2 mb-0">
                    <img width="100%" height="100%" src="/ridiLogoWhole.svg" />
                </section>
                <section className="col-lg-7 d-flex align-items-center justify-content-around">
                    {navLinksDisplay}
                </section>
            </section>
            <section className='d-flex flex-wrap justify-content-between d-lg-none d-block'>
                <section className="col-lg-2 col-md-2 col-4 mb-lg-0">
                    <img width="100%" height="100%" src="/ridiLogoWhole.svg" />
                </section>                   
                <Offcanvas show={offCanvasNav} onHide={hideOffCanvasNav}>
                    <Offcanvas.Header closeButton>
                        <Offcanvas.Title>
                            <section className="col-lg-2 col-auto mb-5  mb-lg-0">
                                <img width="100%" height="100%" src="/ridiLogoWhole.svg" />
                            </section>                                
                        </Offcanvas.Title>
                    </Offcanvas.Header>
                    <Offcanvas.Body>
                        <section>
                            {navLinksDisplay}
                        </section>
                    </Offcanvas.Body>
                </Offcanvas> 
                <button onClick={showOffCanvasNav}>Show Nav</button>       
            </section>
            
            <AlertMsg alertModal={alertModal} />
        </article>
    )
}