import { useEffect, useState } from "react"
import AlertMsg from "../globals/AlertMsg"
import { campaignsRequirements } from "../globals/globalsJs"
import Footer from "../home_page/Footer"
import Navigation from "../Navigation/Navigation"

export default function Campaign({ navigate }){
    const [campaigns, setCampaigns] = useState(campaignsRequirements)
    const [searchFilter, setSearchFilter] = useState('')
    const [alertModal, setAlertModal] = useState(false)

    useEffect(() => {
        if(searchFilter && campaigns){
            const filteredCampaigns = campaignsRequirements.filter(campaign => {
                const { title, caption } = campaign
                return (
                    title.toLocaleLowerCase().includes(searchFilter.toLocaleLowerCase())
                    ||
                    searchFilter.toLocaleLowerCase().includes(title.toLocaleLowerCase())
                    ||
                    caption.toLocaleLowerCase().includes(searchFilter.toLocaleLowerCase())
                    ||
                    searchFilter.toLocaleLowerCase().includes(caption.toLocaleLowerCase())
                )
            })
            if(filteredCampaigns.length > 0){
                setCampaigns(filteredCampaigns)
            } else{
                setCampaigns('No such campaign, try using a different search filter')
            }
        } 
        else{
            setCampaigns(campaignsRequirements)
        }
    }, [searchFilter])

    const onUserSearch = e => setSearchFilter(e.target.value)

    const onUserFilter = e => {
        document.querySelector('#campaign-search-filter').value = ''
        const filter = e.target.value
        if(filter == 'all'){
            setCampaigns(campaignsRequirements)
        }
    }

    const hideAlertModal = () => setAlertModal(false)

    const learnMoreBtnFunc = () => setAlertModal({
        showModal: true, 
        msg: `
            Realhope Campaigns did not just happen. These outreach programs exist because of the
            intellectual masterminds working behind the scenes. The planning, the structuring, 
            the organizing, the thinking process, all performed by people, each a master at his own craft.
            For our programs to see the light, people from far and wide volunteered to handle different sectors and
            once combined, Realhope Campaigns become a thing of Absolute Beauty
        `,
        size: 'xl',
        hideModal: hideAlertModal
    })

    const volunteerForm = () => setAlertModal({
        showModal: true, 
        msg: `
            Still working on our volunteer form for intending volunteers. Do check back later
        `,
        size: 'xl',
        hideModal: hideAlertModal        
    })

    const campaignsDisplay = () => {
        if(typeof campaigns == 'object'){
            return campaigns.map((campaignReq, i) => {
                const { caption, img, title, text, bgImg, link } = campaignReq
                return(
                    <div key={i} style={{backgroundImage: `url(${bgImg})`}} className="single-campaign-bg px-lg-5 px-2">
                        <div className="d-flex flex-wrap align-items-center mb-5 pb-3 justify-content-between">
                            <div className="d-lg-flex flex-wrap justify-content-between align-items-end">
                                <img className="mx-4" src={img} />
                                <div>
                                    <h4 className="campaign-caption">{caption}</h4>
                                    <h1 className="campaign-title">{title}</h1>
                                </div>
                            </div>
                            <div className="my-4">
                                <button className={`
                                    campaign-btn px-4 py-2 rounded-3
                                    ${i == 0 ? 'campaign-1-btn' : 'campaign-others-btn'}
                                    
                                `}>
                                    <a href={link} target="_blank">VISIT</a>
                                </button>
                            </div>
                        </div>
                        <div className="pb-5">
                            <p className="campaign-text text-center">{text}</p>
                        </div>
                    </div>
                )
            })
        }
        return <h1 className="text-center campaign-no-filter my-5 pb-5">{campaigns}</h1>
    }

    return (
        <article className="py-lg-0 my-lg-0">
            <Navigation navigate={navigate} />
            <article className="py-5 my-5">
                <article className="py-lg-5 my-lg-0 my-3 p-lg-2 p-4 mb-5 campaign">
                    <section className="py-lg-5">
                        <div className="mx-lg-5 py-lg-5 px-lg-5">
                            <h1 className="mb-4 campaigns-header px-4">Campaigns</h1>
                            <p className="my-4 col-lg-8 campaigns-header-text">
                                Everything Realhope stands for in one place. View or campaigns featuring outreach programs
                                events and seminars carried out by Realhope for the benefit of street children. Being a non-profit,
                                everything done by Realhope is for the betterment of the abandoned populace of the world: The street 
                                children                  
                            </p>
                        </div>
                        <div className="d-flex justify-content-end px-lg-2 px-0">
                            <div className="d-flex">
                                <div className="input-group">
                                    <input type="text" id="campaign-search-filter"
                                        onInput={e => onUserSearch(e)} className="form-control" placeholder="Filter by title or date" 
                                    />
                                    <div className="input-group-append">
                                        <span className="input-group-text">@</span>
                                    </div>
                                </div>                        
                                <select style={{backgroundImage: `url('/sortIcon.svg')`}} 
                                    onClick={e => onUserFilter(e)} className="mx-lg-5 col-lg-auto col-3 select-with-icon"
                                >
                                    <option value='all'>All</option>
                                    <option value="1">One</option>
                                    <option value="2">Two</option>
                                    <option value="3">Three</option>
                                </select>
                            </div>
                        </div>
                    </section>
                    <section className="py-5 px-lg-4 my-5">
                        {campaignsDisplay()}
                    </section>
                    <section className="mb-5 p-3 px-5 mx-3 text-center">
                        <h3 className="mb-5 pb-5 campaign-join-header">Wondering how you can be a part of our Campaigns</h3>
                        <div className="mb-5 pb-lg-5 pb-3 d-flex flex-wrap align-items-center justify-content-around">
                            <div>
                                <img src="/twoVolunteersIcon.svg" className="mb-2" />
                                <h4 className="campaign-join-icon-labels">VOLUNTEER</h4>
                            </div>
                            <hr className="yellow-line col-lg-2" />
                            <div>
                                <img src="/singleVolunteerIcon.svg" className="mb-2" />
                                <h4 className="campaign-join-icon-labels">PARTNER</h4>
                            </div>
                            <hr className="yellow-line col-lg-2" />
                            <div>
                                <img src="/donateIcon.svg" className="mb-2" />
                                <h4 className="campaign-join-icon-labels">DONATE</h4>
                            </div>
                        </div>
                        <div className="d-flex-justify-content-center flex wrap">
                            <button onClick={() => learnMoreBtnFunc()}
                            className="mx-lg-3 mx-0 mx-md-2 btn campaign-join-learn-more-btn py-3 px-3 mb-2">LEARN MORE</button>
                            <button  onClick={() => volunteerForm()}
                            className="mx-lg-3 mx-0 mx-md-2 btn campaign-join-lets-do-it-btn py-3 px-3 px-lg-5 mb-2">VOLUNTEER</button>
                        </div>
                    </section>
                </article>
            </article>
            {
                alertModal && <AlertMsg alertModal={alertModal} />
            }
            <Footer navigate={navigate} />            
        </article>
    )

}