import { contactAddresses } from "../globals/globalsJs"
import { Formik, Form, Field } from 'formik'

export default function Contact(){
    const addressDisplay = contactAddresses.map((address, i) => {
        const { title, text } = address
        return(
            <div className="mb-3">
                <h5 className="contact-main-sub-header">{title}</h5>
                <p className="contact-main-text">{text}</p>
            </div>            
        )
    })

    return(
        <article className="py-5 contact">
            <section className="py-5">
                <div className="mb-5 pb-5">
                    <h1 className="m-5 px-4 pb-5">Contact Us</h1>
                </div>
                <section className="contact-main my-5 p-5 d-flex flex-wrap justify-content-around">
                    <div className="col-lg-4 p-5 my-5">
                        <h1 className="mb-5 contact-main-header">We will love to indulge</h1>
                        <div className="mb-5">
                            {addressDisplay}
                        </div>
                        <div className="pb-5 mb-1">
                            <hr className="yellow-line" />
                        </div>
                        <div className="d-flex justify-content-between flex-wrap col-lg-6">
                            <img src="/whatsappIconOutline.svg" />
                            <img src="/instagramIconOutline.svg" />
                            <img src="/twitterIconOutline.svg" />
                        </div>
                    </div>
                    <div className="col-lg-6 contact-main-form d-flex flex-column align-items-center justify-content-center p-5">
                        <div className="py-5 my-4">
                            <Formik
                                initialValues={{
                                    fullName: '', email: '', message: ''
                                }}
                                onSubmit={(values, { resetForm }) => {

                                }}
                            >
                                {
                                    ({values, handleSubmit, errors, touched, handleBlur, handleChange, isValid, dirty}) => (
                                        <Form className="py-5" onSubmit={handleSubmit}>
                                            <div  className="mb-4 d-flex justify-content-between">
                                                <div className="mb-3 col-lg-5 d-flex flex-column">
                                                    <label className="contact-main-form-labels" htmlFor='fullName'>FULL NAME</label>
                                                    <Field
                                                        type="text"
                                                        name="fullName"
                                                        className="fw-bold"
                                                        autoComplete="off"
                                                        onChange={handleChange}
                                                        onBlur={handleBlur}
                                                        value={values.fullName}
                                                    />
                                                    { errors.fullName && touched.fullName && <p className='text-danger fst-italic fw-bold'>{errors.fullName}</p> }
                                                </div>                            
                                                <div className="mb-3 col-lg-5 d-flex flex-column">
                                                    <label htmlFor='email' className="contact-main-form-labels">EMAIL ADDRESS</label>
                                                    <Field
                                                        type="email"
                                                        name="email"
                                                        className="fw-bold"
                                                        autoComplete="off"
                                                        onChange={handleChange}
                                                        onBlur={handleBlur}
                                                        value={values.email}
                                                    />
                                                    { errors.email && touched.email && <p className='text-danger fst-italic fw-bold'>{errors.email}</p> }
                                                </div> 
                                            </div>

                                            <div className="mb-3 d-flex flex-column">
                                                <label htmlFor='message' className="contact-main-form-labels">MESSAGE</label>
                                                <textarea
                                                    name="message"
                                                    className="fw-bold"
                                                    style={{height: '30vh'}}
                                                    autoComplete="off"
                                                    onChange={handleChange}
                                                    onBlur={handleBlur}
                                                    value={values.message}
                                                />
                                                { errors.message && touched.message && <p className='text-danger fst-italic fw-bold'>{errors.message}</p> }
                                            </div>                          
                                            <button type="submit" value="submit" className='contact-main-form-btn w-100' disabled={!isValid || !dirty}>SUBMIT</button>
                                        </Form>
                                    )
                                }
                            </Formik>
                            <div className="">
                                <div className="d-flex align-items-center">
                                    <img src="/mailIcon.svg" />
                                    <p className="mx-4 contact-main-form-links">support@realhopeinitiative.org</p>
                                </div>
                                <div className="d-flex align-items-center">
                                    <img src="/phoneIcon.svg" />
                                    <p className="mx-4 contact-main-form-links">+234 8039 718 269</p>
                                </div>                                  
                            </div>
                        </div>                        
                    </div>
                </section>
            </section>
        </article>
    )
}