import { useState } from "react";
import Overview from "./Overview";
import Navigation from '../Navigation/Navigation'
import { Tabs, Tab } from 'react-bootstrap'
import Achievements from "./Achievements";
import Footer from "../home_page/Footer";
import TheTeam from "./TheTeam";
import Gallery from "./Gallery";

export default function About({ navigate }){
    const [aboutActiveTab, setAboutActiveTab] = useState('overview')

    return(
        <article>
            <Navigation navigate={navigate} />
            <article className="about py-lg-5 py-5 my-lg-5 my-5 spacing">
                <section className="px-3 py-lg-5 p-0">
                    <Tabs
                        id="controlled-tab-example"
                        activeKey={aboutActiveTab}
                        onSelect={(k) => setAboutActiveTab(k)}
                        className="mb-5 d-flex justify-content-around"         
                    >
                        <Tab eventKey="overview" title={<h2 className="about-tab-title">Overview</h2>}>
                            <Overview />
                        </Tab>
                        <Tab eventKey="Achievement" title={<h2 className="about-tab-title">Achievement</h2>}>
                            <Achievements />
                        </Tab>
                        <Tab eventKey="theTeam" title={<h2 className="about-tab-title">Meet the Team</h2>}>
                            <TheTeam />
                        </Tab>
                        <Tab eventKey="gallery" title={<h2 className="about-tab-title">Gallery</h2>}>
                            <Gallery />
                        </Tab>                    
                    </Tabs>  
                </section>          
            </article>
            <Footer navigate={navigate} />
        </article>
    )
}