import React from "react";
import { teamSections } from "../globals/globalsJs";

export default function TheTeam(){

    const displayTeamSections = teamSections.map(section => {
        const { sectionTitle, teamMembers } = section
        return (
            <div className="py-lg-4 mx-0 mx-lg-5 my-lg-4 my-2 py-2">
                <h1 className="theTeam-section-title w-lg-50 w-75 mb-lg-0 mb-5 pb-3">{sectionTitle}</h1>
                <div style={{gridGap: '90px'}} className="d-flex flex-wrap my-3 justify-content-center align-items-center text-center">
                    {
                        teamMembers.map(member => {
                            const { img, bgImg, imgTitle, imgText } = member
                            return (
                                <div className="my-lg-5 my-0 col-md-3 col-lg-3 col-7 py-lg-5 py-2">
                                    <div className="mb-lg-3 mb-3 bgImages" style={{backgroundImage: `url(${bgImg})`}}>
                                        <img className="col-lg-8 col-12 col-md-12" src={img} />                            
                                    </div>
                                    <h3 className="theTeam-img-title">{imgTitle}</h3>
                                    <p className="text-muted theTeam-img-text">{imgText}</p>
                                </div>
                            )
                        })
                    }                    
                </div>
            </div>
        )
    })

    return (
        <article className="mt-5 px-lg-5 px-2 pt-5">
            <h1 className="mx-lg-5 px-4 about-theTeam-header">
                Meet The Team Cranking Things Up
            </h1>

            <div className="mt-5 py-3">
                {displayTeamSections}
            </div>
        </article>
    )
}