import { useEffect } from "react"
import { Modal } from "react-bootstrap"

export default function AlertMsg({ alertModal }){
    const { showModal, msg, hideModal, duration, size } = alertModal

    useEffect(() => {
        if(showModal && duration){
            const hideModalInterval = setTimeout(() => {
                hideModal()
                clearTimeout(hideModalInterval)
            }, duration)
        }
    }, [showModal])

    return(
        <Modal size={size} show={showModal} onHide={hideModal}>
            <Modal.Header closeButton>
                <h3 className="modal-header">Realhope Initiative</h3>
            </Modal.Header>

            <Modal.Body>
                <p className="modal-msg">{msg}</p>
            </Modal.Body>

            <Modal.Footer>
                <img className="col-lg-2 col-2 col-md-2" src="/ridiLogoWhole.svg" />
            </Modal.Footer>
        </Modal>
    )
}