import { aboutAchievements } from "../globals/globalsJs"

export default function Achievements(){
    
    const achievementsDisplay = aboutAchievements.map((achievement, i) => {
        const { title, text, img, imgTxt } = achievement
        return(
            <div className="d-flex flex-wrap pb-5 align-items-center">
                <div className="col-lg-3 col-12 py-5 px-2 about-achievement-icon-cont mb-5">
                    <div className="py-5 text-center">
                        <img src={img} className="mb-5" /> 
                        <p className="text-center pb-lg-5 pb-2 about-achievement-img-text">{imgTxt}</p>                        
                    </div>
                </div>
                <div className="col-lg-8 col-12 mx-lg-4 mx-0">
                    <h2 className="mb-2 about-achievement-title">{title}</h2>
                    <p className="about-achievement-text">{text}</p>
                    <p className="goals-text-more achievements-read-more">Learn How</p>
                </div>
            </div>
        )
    })

    return(
        <article className="mt-5 px-lg-5 px-2 pt-5">
            <h1 className="mx-lg-5 px-4 about-achievement-header">Taking a look at what we've done so far</h1>
            <section style={{gridGap: '90px'}} className="d-flex flex-wrap mt-5 mx-lg-5  py-3 justify-content-center flex-column align-items-center">
                {achievementsDisplay}
            </section>
        </article>
    )
}