export const arrOfObj = ({ selectSingle, add, addKey, addValue, remove, select, arr, value, match }) => {
    const arrClone = [...arr]
    const selects = []
    for(let i = 0; i < arrClone.length; i++){
        if(value == arrClone[i][match]){
            if(remove){
                return arrClone.splice(arrClone.indexOf(arrClone[i]), 1)
            }
            if(selectSingle){
                return arrClone[i]
            }
            if(select){
                selects.push(arrClone[i])
            }
            if(add){
                if(arrClone[i][addKey]){
                    arrClone[i][addKey].push(addValue[i])
                } else{
                    arrClone[i][addKey] = addValue
                }
                return arrClone
            }
        } 
    }
    if(select){
        return selects
    }
}