export default function Header({ navigateTo }){
    return(
        <article style={{ paddingTop: '15%' }} className="main-header my-lg-0 my-5 spacing d-flex px-lg-3 px-3 flex-wrap align-items-center justify-content-center">
            <section className="col-lg-6 mt-lg-0 mt-5 mb-lg-0">
                <h1 className="mb-4">
                    We are building sustainable oppurtunities for vulnerable childeren 
                </h1>
                <p className="main-header-text mb-5 pb-2">
                    The ignored, the abandoned populace of our country, the stigmatized,
                    the "street children". We are here to give them REAL HOPE                   
                </p>
                <button onClick={() => navigateTo('campaigns')} 
                className="d-flex justify-content-center main-header-btn px-lg-5 py-lg-3 px-md-3 py-md-1 px-3 py-1">
                    LEARN HOW
                </button>
            </section>
            <section className="col-lg-3 mx-3 my-3 my-lg-0 main-header-img-cont">
                <img className="main-header-img" width="100%" height="100%" src="/mainHeader.png"/>
            </section>
        </article>
    )
}